import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { Status } from 'src/app/shared/consts/Status.const';

import { LotericaDTO } from 'src/app/shared/interfaces/lotericas/LotericaDTO';
import { PerfilDTO } from 'src/app/shared/interfaces/perfis/PerfilDTO';
import { UserDTO } from 'src/app/shared/interfaces/users/UserDTO';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';
import { PerfilService } from 'src/app/shared/services/perfil/perfil.service';

@Component({
  selector: 'app-funcionario-detail',
  templateUrl: './funcionario-detail.component.html',
  styleUrls: ['./funcionario-detail.component.scss'],
})
export class FuncionarioDetailComponent implements OnInit, OnDestroy {

  @Input()
  user: UserDTO;

  form: FormGroup;

  perfis: PerfilDTO[] = [];
  lotericas: LotericaDTO[] = [];
  listStatus: any[] = [];

  isLoaded = false;

  createdUserId = '';
  userWasCreated = false;

  private subscriptions = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private funcionarioService: FuncionarioService,
    private lotericaService: LotericaService,
    private perfilService: PerfilService,
    private modal: ModalController,
  ) {
  }

  ionViewWillEnter() {
    this.listStatus = Status;
    this.loadLotericas();
    this.loadPerfis();
  }

  ngOnInit() {
    const { nome, email, is_admin, perfil, status } = this.user;

    this.form = this.formBuilder.group({
      nome: [nome, [Validators.required]],
      email: [email, [Validators.required, Validators.email]],
      senha: [, [Validators.required,]],
      is_admin: [is_admin, [Validators.required,]],
      status: [status, [Validators.required,]],
      perfil_id: [{ value: perfil }, [Validators.required,]],
      loterica_id: [perfil, [Validators.required,]],
    });
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadLotericas() {
    const sub = this.lotericaService.list()
      .subscribe(data => {
        this.lotericas = data;
        const { unidadeLoterica } = this.user;
        this.form.get("loterica_id").setValue(this.lotericas.find(p => p.id === unidadeLoterica.id));
      });
    this.subscriptions.add(sub);
  }

  loadPerfis() {
    const sub = this.perfilService.list()
      .subscribe(data => {
        this.perfis = data;
        const { perfil, status } = this.user;

        if(perfil){
          this.form.get("perfil_id").setValue(this.perfis.find(p => p.id === perfil.id));
        }

        this.form.get("status").setValue(this.listStatus.find(s => s.value === status));
        this.isLoaded = true;
      });

    this.subscriptions.add(sub);
  }

  update() {
    const { id } = this.user;
    const nome = this.form.get('nome').value;
    const email = this.form.get('email').value;
    const senha = this.form.get('senha').value;
    const is_admin = this.form.get('is_admin').value;
    const status = this.form.get('status').value.value;
    const { id: perfil_id } = this.form.get('perfil_id').value;
    const { id: unidadeLoterica } = this.form.get('loterica_id').value;

    const sub = this.funcionarioService.updateUser({ nome, email, senha, is_admin, status, perfil_id, unidadeLoterica }, id)
      .subscribe(response => {
        this.fechar(true);
      });
    this.subscriptions.add(sub);
  }

  fechar(userWasCreated = false) {
    this.modal.dismiss(userWasCreated);
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableBTN(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

}