import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/config/api.config';
import { CreatePatrimonioDTO } from '../../interfaces/patrimonios/CreatePatrimonioDTO';
import { PatrimonioDTO } from '../../interfaces/patrimonios/PatrimonioDTO';
import { UpdatePatrimonioDTO } from '../../interfaces/patrimonios/UpdatePatrimonioDTO';

@Injectable({
  providedIn: 'root'
})
export class PatrimonioService {

  constructor(
    private http: HttpClient,
  ) { }

  list(loterica_id: string): Observable<PatrimonioDTO[]> {
    const params = new HttpParams()
      .set('loterica_id', loterica_id);
    return this.http.get<PatrimonioDTO[]>(`${API_CONFIG.baseURL}/patrimonio`, {
      params,
    });
  }

  create(newPatrimonio: CreatePatrimonioDTO): Observable<any> {
    return this.http.post<any>(
      `${API_CONFIG.baseURL}/patrimonio`,
      newPatrimonio,
      {
        responseType: 'json',
      });
  }
  //TODO - Implementar o update
  update(patrimonio_id: string, newPatrimonio: UpdatePatrimonioDTO): Observable<any> {
    return this.http.put<any>(
      `${API_CONFIG.baseURL}/patrimonio/${patrimonio_id}`,
      newPatrimonio,
      {
        responseType: 'json',
      });
  }

  associate(patrimonio_id: string, id_fisico: string): Observable<any> {
    return this.http.patch<any>(
      `${API_CONFIG.baseURL}/patrimonio/${patrimonio_id}`,
      {
        id_fisico
      },
      {
        responseType: 'json',
      });
  }

  desassociar(patrimonio_id: string): Observable<any> {
    return this.http.patch<any>(
      `${API_CONFIG.baseURL}/patrimonio/${patrimonio_id}/desassociar`,
      {
        responseType: 'json',
      });
  }
}
