import { Component, OnDestroy, OnInit } from '@angular/core';
import { Device } from '@capacitor/device';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';
import { ConfirmationService, MessageService, SelectItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { LotericaDTO } from 'src/app/shared/interfaces/lotericas/LotericaDTO';
import { PatrimonioDTO } from 'src/app/shared/interfaces/patrimonios/PatrimonioDTO';
import { StorageService } from 'src/app/shared/services/auth/storage.service';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';
import { PatrimonioService } from 'src/app/shared/services/patrimonio/patrimonio.service';
import { PatrimonioCadastroComponent } from '../patrimonio-cadastro/patrimonio-cadastro.component';
import { PatrimonioDetailComponent } from '../patrimonio-detail/patrimonio-detail.component';

@Component({//TODO CONTINUAR PATIRMONIO
  selector: 'app-patrimonio-main',
  templateUrl: './patrimonio-main.component.html',
  styleUrls: ['./patrimonio-main.component.scss'],
})
export class PatrimonioMainComponent implements OnInit, OnDestroy {

  patrimonios: PatrimonioDTO[] = [];
  sortLotericas: SelectItem[] = [];

  currentLotericaLoaded: string;

  isLoaded = false;

  idFisicoDispositivo: string;

  private subscriptions = new Subscription();

  constructor(
    private patrimonioService: PatrimonioService,
    private lotericaService: LotericaService,
    private modal: ModalController,
    private toastService: MessageService,
    private confirmationService: ConfirmationService,
  ) { }

  async ngOnInit() {
    this.idFisicoDispositivo = await this.getCurrentIdFisico();
    console.log(this.idFisicoDispositivo);
    this.loadLotericas();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadLotericas() {
    const sub = this.lotericaService.list()
      .subscribe(data => {
        this.sortLotericas = data.map((l) => {
          const { id, nome, codigo } = l;
          return {
            value: id,
            label: `${nome} - ${codigo}`
          }
        });
      });
    this.subscriptions.add(sub);
  }

  lotericaLabel(loterica: LotericaDTO) {
    return `${loterica.nome} - ${loterica.codigo}`;
  }

  changeFiltro(event) {
    const { value: loterica_id } = event;
    this.currentLotericaLoaded = loterica_id;
    this.loadPatrimonioByLoterica(loterica_id);
  }

  loadPatrimonioByLoterica(loterica_id: string) {
    const sub = this.patrimonioService.list(loterica_id)
      .subscribe(response => {
        this.patrimonios = response;
        this.isLoaded = true;
      });
    this.subscriptions.add(sub);
  }

  createNewpatrimonio() {
    this.showModal(PatrimonioCadastroComponent);
  }

  editarPatrimonio(patrimonio: PatrimonioDTO) {
    this.showModal(PatrimonioDetailComponent, { patrimonio, loterica_id: this.currentLotericaLoaded });
  }

  associar(patrimonio: PatrimonioDTO) {
    this.confirmationService.confirm({
      message: 'Deseja associar o dispositivo em que está logado a este patrimônio?',
      header: 'Confirmação',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.patrimonioService.associate(patrimonio.id, this.idFisicoDispositivo)
          .subscribe(data => {
            this.loadPatrimonioByLoterica(this.currentLotericaLoaded);
            this.presentToast('success', 'Sucesso!', 'Associado com sucesso!', Toast.mediumDuration);
          });
      },
      reject: (type) => {
        this.toastService.clear();
      }
    });
  }


  desassociar(patrimonio: PatrimonioDTO) {
    this.confirmationService.confirm({
      message: 'Deseja desassociar o dispositivo este patrimônio?',
      header: 'Confirmação',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.patrimonioService.desassociar(patrimonio.id)
          .subscribe(data => {
            this.loadPatrimonioByLoterica(this.currentLotericaLoaded);
            this.presentToast('success', 'Sucesso!', 'Desassociado com sucesso!', Toast.mediumDuration);
          });
      },
      reject: (type) => {
        this.toastService.clear();
      }
    });
  }

  async getCurrentIdFisico() {
    const { uuid } = await Device.getId();
    return uuid;
  }


  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-80',
      componentProps,
    });

    modal.onDidDismiss()
      .then((data) => {
        const { data: hasUpdate } = data;
        if (hasUpdate) {
          this.presentToast('success', 'Sucesso!', 'Operação bem sucedida!', Toast.mediumDuration);
        }

        if (data.role) {
          const { loterica_id } = data.role as any;
          this.loadPatrimonioByLoterica(loterica_id);
          this.presentToast('success', 'Sucesso!', 'Operação bem sucedida!', Toast.mediumDuration);
        }


      });

    return await modal.present();
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }

}
