import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { MenuItem, MessageService } from 'primeng/api';
import { Subscription } from 'rxjs';
import { LotericaDTO } from 'src/app/shared/interfaces/lotericas/LotericaDTO';
import { PerfilDTO } from 'src/app/shared/interfaces/perfis/PerfilDTO';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';
import { PerfilService } from 'src/app/shared/services/perfil/perfil.service';

@Component({
  selector: 'app-funcionario-cadastro',
  templateUrl: './funcionario-cadastro.component.html',
  styleUrls: ['./funcionario-cadastro.component.scss'],
})
export class FuncionarioCadastroComponent implements OnInit, OnDestroy {

  form: FormGroup;
  perfis: PerfilDTO[] = [];
  lotericas: LotericaDTO[] = [];
  isLoaded = false;

  steps: MenuItem[];
  stepIndex = 0;

  createdUserId = '';
  userWasCreated = false;

  private subscriptions = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private funcionarioService: FuncionarioService,
    private lotericaService: LotericaService,
    private perfilService: PerfilService,
    private modal: ModalController,

  ) {
    this.form = this.formBuilder.group({
      nome: [, [Validators.required]],
      email: [, [Validators.required, Validators.email]],
      senha: [, [Validators.required,]],
      cpf: [, [Validators.required,]],
      perfil_id: [, [Validators.required,]],
      loterica_id: [, [Validators.required,]],
    });
  }

  ionViewWillEnter() {
    this.loadLotericas();
    this.loadPerfis();
  }

  ngOnInit() {
    this.steps = [
      {
        label: 'Informações',
      },
      {
        label: 'Endereço',
      },
    ];
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadLotericas() {
    const sub = this.lotericaService.list()
      .subscribe(data => {
        this.lotericas = data;
        this.isLoaded = true;
      });

    this.subscriptions.add(sub);
  }

  loadPerfis() {
    const sub = this.perfilService.list()
      .subscribe(data => {
        this.perfis = data;
        this.isLoaded = true;
      });
    this.subscriptions.add(sub);
  }

  create() {
    const nome = this.form.get('nome').value;
    const email = this.form.get('email').value;
    const senha = this.form.get('senha').value;
    const cpf = this.form.get('cpf').value;
    const { id: perfilId } = this.form.get('perfil_id').value;
    const { id: loterica_id } = this.form.get('loterica_id').value;

    const sub = this.funcionarioService.createUser({ nome, email, senha, cpf, perfilId, loterica_id })
      .subscribe(response => {
        this.createdUserId = response.id;
        this.userWasCreated = true;
        this.stepIndex++;
      });
    this.subscriptions.add(sub);
  }


  listenEndereco(event) {
    this.fechar(this.userWasCreated);
  }

  fechar(userWasCreated = false) {
    this.modal.dismiss(userWasCreated);
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableBTN(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }
}
