import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { StatusPatrimonio } from 'src/app/shared/consts/Status.const';
import { LotericaDTO } from 'src/app/shared/interfaces/lotericas/LotericaDTO';
import { PatrimonioDTO } from 'src/app/shared/interfaces/patrimonios/PatrimonioDTO';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';
import { PatrimonioService } from 'src/app/shared/services/patrimonio/patrimonio.service';

@Component({
  selector: 'app-patrimonio-detail',
  templateUrl: './patrimonio-detail.component.html',
  styleUrls: ['./patrimonio-detail.component.scss'],
})
export class PatrimonioDetailComponent implements OnInit, OnDestroy {

  @Input()
  patrimonio: PatrimonioDTO;

  @Input()
  loterica_id: string;

  form: FormGroup;
  lotericas: LotericaDTO[] = [];

  listStatus = StatusPatrimonio;

  isLoaded = false;

  private subscriptions = new Subscription();

  constructor(
    private formBuilder: FormBuilder,
    private patrimonioService: PatrimonioService,
    private lotericaService: LotericaService,
    private modal: ModalController,
  ) {
  }

  ngOnInit() {
    const { nome, descricao, valor_patrimonio, id_fisico, status_patrimonio } = this.patrimonio;

    this.form = this.formBuilder.group({
      nome: [nome, [Validators.required]],
      descricao: [descricao, [Validators.required]],
      valor_patrimonio: [valor_patrimonio, [Validators.required,]],
      loterica_id: [this.loterica_id, [Validators.required,]],
      status_patrimonio: [status_patrimonio, [Validators.required,]],
      id_fisico: [],
    });

    this.loadLotericas();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  loadLotericas() {
    const { status_patrimonio } = this.patrimonio;
    const sub = this.lotericaService.list()
      .subscribe(data => {
        this.lotericas = data;
        this.form.get("loterica_id").setValue(this.lotericas.find(l => l.id === this.loterica_id));
        this.form.get("status_patrimonio").setValue(this.listStatus.find(s => s.value === status_patrimonio));
        this.isLoaded = true;
      });

    this.subscriptions.add(sub);
  }

  update() {
    const { id } = this.patrimonio;
    const nome = this.form.get('nome').value;
    const descricao = this.form.get('descricao').value;
    const valor_patrimonio = this.form.get('valor_patrimonio').value;
    const id_fisico = this.form.get('id_fisico').value;
    const status_patrimonio = this.form.get('status_patrimonio').value.value;
    const { id: loterica_id } = this.form.get('loterica_id').value;

    let idOfThisPlataform = undefined;
    if (id_fisico) {
      // TODO - Funcao que retorne o UUID atual da maquina: idOfThisPlataform = 'UUID';
    }

    const sub = this.patrimonioService.update(id, { nome, descricao, valor_patrimonio, id_fisico: idOfThisPlataform, loterica_id, status_patrimonio })
      .subscribe(response => {
        this.fechar(true, { loterica_id });
      });


    this.subscriptions.add(sub);
  }

  fechar(wasCreated = false, data?: any) {
    this.modal.dismiss(wasCreated, data);
  }

  isInputError(inputName: string): boolean {
    const resp =
      this.form.controls[inputName].dirty &&
      this.form.controls[inputName].errors;

    if (resp) {
      return true;
    }
    return false;
  }

  disableBTN(): boolean {
    const situacao = !this.form.valid;

    return situacao;
  }

}
