import { Component, OnDestroy, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ComponentProps, ComponentRef } from '@ionic/core';

import { MessageService, SelectItem } from 'primeng/api';
import { Subscription } from 'rxjs';
import { Toast } from 'src/app/shared/enumerations/toast.enum';
import { UserDTO } from 'src/app/shared/interfaces/users/UserDTO';
import { FuncionarioService } from 'src/app/shared/services/funcionario/funcionario.service';
import { LotericaService } from 'src/app/shared/services/loterica/loterica.service';
import { EnderecoComponent } from '../../endereco-module/endereco/endereco.component';
import { UpdateEnderecoComponent } from '../../endereco-module/update-endereco/update-endereco.component';
import { FuncionarioCadastroComponent } from '../funcionario-cadastro/funcionario-cadastro.component';
import { FuncionarioDetailComponent } from '../funcionario-detail/funcionario-detail.component';

@Component({
  selector: 'app-funcionario-main',
  templateUrl: './funcionario-main.component.html',
  styleUrls: ['./funcionario-main.component.scss'],
})
export class FuncionarioMainComponent implements OnInit, OnDestroy {

  users: UserDTO[] = [];
  sortOptions: SelectItem[] = [];
  sortLotericas: SelectItem[] = [];

  isLoaded = false;
  currentLotericaLoaded: string;

  private subscriptions = new Subscription();

  constructor(
    private funcionarioService: FuncionarioService,
    private modal: ModalController,
    private toastService: MessageService,
    private lotericaService: LotericaService,
  ) { }

  ngOnInit() {
    this.sortOptions = [
      { label: 'Todos os usuários', value: 'all' },
      { label: 'Apenas usuários logados', value: 'logged' }
    ];
    this.loadLotericas();
    //this.loadFuncionarios();
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  changeFiltro(event) {
    if (event.value === 'logged') {
      this.loadLoggedFuncionarios();
      return;
    }
    this.loadFuncionarios();
  }

  changeLoterica(event) {
    const { value: loterica_id } = event;
    this.currentLotericaLoaded = loterica_id;
  }

  loadLotericas() {
    const sub = this.lotericaService.list()
      .subscribe(data => {
        this.sortLotericas = data.map((l) => {
          const { id, nome, codigo } = l;
          return {
            value: id,
            label: `${nome} - ${codigo}`
          }
        });
      });
    this.subscriptions.add(sub);
  }

  loadFuncionarios() {
    const loterica_id = this.currentLotericaLoaded;
    if(!loterica_id){
      this.presentToast('warn', 'Lotérica inválida', 'Selecione uma lotérica para continuar', Toast.shortDuration);
      return;
    }

    const sub = this.funcionarioService.listAllUsers(loterica_id)
      .subscribe(response => {
        this.users = response;
        this.isLoaded = true;
      });
    this.subscriptions.add(sub);
  }

  loadLoggedFuncionarios() {
    const loterica_id = this.currentLotericaLoaded;
    
    if(!loterica_id){
      this.presentToast('warn', 'Lotérica inválida', 'Selecione uma lotérica para continuar', Toast.shortDuration);
      return;
    }

    const sub = this.funcionarioService.listAllLogedUsers(loterica_id)
      .subscribe(response => {
        this.users = response;
        this.isLoaded = true;
      });
    this.subscriptions.add(sub);
  }

  detalharFuncionario(user: UserDTO) {
    this.showModal(FuncionarioDetailComponent, { user });
  }

  detalharEndereco(user: UserDTO) {
    if (user.endereco) {
      this.showModal(UpdateEnderecoComponent, { currentEndereco: user, isFuncionario: true });
    } else {
      this.showModal(EnderecoComponent, { idOf: user.id, isFuncionario: true, isModal: true });
    }
  }

  setUserImage(user: UserDTO) {
    return user.avatar || "assets/imgs/outros/default-user.jpg";
  }

  createNewUser() {
    this.showModal(FuncionarioCadastroComponent);
  }

  private async showModal(
    component: ComponentRef,
    componentProps?: ComponentProps
  ) {
    const modal = await this.modal.create({
      component,
      backdropDismiss: false,
      cssClass: 'modal-size-80',
      componentProps,
    });

    modal.onDidDismiss().then((data) => {
      const { data: hasUpdate } = data;
      if (hasUpdate) {
        this.isLoaded = false;
        this.loadFuncionarios();
        this.presentToast('success', 'Sucesso!', 'Operação bem sucedida!', Toast.mediumDuration);
      }
    });

    return await modal.present();
  }

  private presentToast(
    severity: string,
    summary: string,
    detail: string,
    life: Toast
  ) {
    this.toastService.clear();
    this.toastService.add({
      severity,
      summary,
      detail,
      life,
    });
  }


}
